<template>
	<div id="dashboard">
		<table class="striped">
			<thead class="orange darken-4 white-text">
				<tr>
					<th>Month</th>
					<th>Jan <div>{{totalJan}}</div></th>
					<th>Feb <div>{{totalFeb}}</div></th>
					<th>Mar <div>{{totalMar}}</div></th>
					<th>Apr <div>{{totalApr}}</div></th>
					<th>May <div>{{totalMay}}</div></th>
					<th>Jun <div>{{totalJun}}</div></th>
					<th>Jul <div>{{totalJul}}</div></th>
					<th>Aug <div>{{totalAug}}</div></th>
					<th>Sep <div>{{totalSep}}</div></th>
					<th>Oct <div>{{totalOct}}</div></th>
					<th>Nov <div>{{totalNov}}</div></th>
					<th>Dec <div>{{totalDec}}</div></th>
					<th></th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="logistic in logistics" v-bind:key="logistics.id">
					<td>{{logistic.month}}</td>
					<td>{{logistic.january}}<div>{{logistic.january_percent}}%</div></td>
					<td>{{logistic.february}}<div>{{logistic.february_percent}}%</div></td>
					<td>{{logistic.march}}<div>{{logistic.march_percent}}%</div></td>
					<td>{{logistic.april}}<div>{{logistic.april_percent}}%</div></td>
					<td>{{logistic.may}}<div>{{logistic.may_percent}}%</div></td>
					<td>{{logistic.june}}<div>{{logistic.june_percent}}%</div></td>
					<td>{{logistic.july}}<div>{{logistic.july_percent}}%</div></td>
					<td>{{logistic.august}}<div>{{logistic.august_percent}}%</div></td>
					<td>{{logistic.september}}<div>{{logistic.september_percent}}%</div></td>
					<td>{{logistic.october}}<div>{{logistic.october_percent}}%</div></td>
					<td>{{logistic.november}}<div>{{logistic.november_percent}}%</div></td>
					<td>{{logistic.december}}<div>{{logistic.december_percent}}%</div></td>
					<td>
						<router-link v-bind:to="{name: 'view-logistics', params: {logistics_id: logistic.logistics_id}}" class="secondary-content"><i class="fa fa-eye"></i></router-link>
					</td>
				</tr>
			</tbody>
		</table>
<!-- 		<div class="fixed-action-btn">
			<router-link to="/new-logistics" class="btn-floating btn-large red">
				<i class="fa fa-plus"></i>
			</router-link>
		</div> -->
	</div>
</template>

<script>
	import db from './firebaseInit'
	export default {
		name: 'dashboard',
		data () {
			return {
				logistics: []
			}
		},
		created () {
			db.collection('logistics').orderBy('logistics_id').get().then
			(querySnapshot => {
				querySnapshot.forEach(doc => {
					// console.log(doc.data());
					const data = {
						'id': doc.id,
						'logistics_id': doc.data().logistics_id,
						'month': doc.data().month,
						'january': doc.data().january,
						'february': doc.data().february,
						'march': doc.data().march,
						'april': doc.data().april,
						'may': doc.data().may,
						'june': doc.data().june,
						'july': doc.data().july,
						'august': doc.data().august,
						'september': doc.data().september,
						'october': doc.data().october,
						'november': doc.data().november,
						'december': doc.data().december,
						'january_percent': doc.data().january_percent,
						'february_percent': doc.data().february_percent,
						'march_percent': doc.data().march_percent,
						'april_percent': doc.data().april_percent,
						'may_percent': doc.data().may_percent,
						'june_percent': doc.data().june_percent,
						'july_percent': doc.data().july_percent,
						'august_percent': doc.data().august_percent,
						'september_percent': doc.data().september_percent,
						'october_percent': doc.data().october_percent,
						'november_percent': doc.data().november_percent,
						'december_percent': doc.data().december_percent
					}
					this.logistics.push(data)
				})
			})
		},
		computed: {
			totalJan() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.january
				}, 0)
			},
			totalFeb() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.february
				}, 0)
			},
			totalMar() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.march
				}, 0)
			},
			totalApr() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.april
				}, 0)
			},
			totalMay() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.may
				}, 0)
			},
			totalJun() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.june
				}, 0)
			},
			totalJul() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.july
				}, 0)
			},
			totalAug() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.august
				}, 0)
			},
			totalSep() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.september
				}, 0)
			},
			totalOct() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.october
				}, 0)
			},
			totalNov() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.november
				}, 0)
			},
			totalDec() {
				return this.logistics.reduce(function (sum, item) {
					return sum + item.december
				}, 0)
			}
		}
	}
</script>