<template>
	<div id="edit-cs">
		<form @submit.prevent="updateCs"class="">
			<table>
				<thead class="orange darken-4 white-text">
					<tr>
						<th>DAYS</th>
						<th>CUSTOMER SERVICE</th>
						<th>LOGISTICS</th>
						<th>STORE</th>
						<th>TOTAL</th>
						<th>PREV WEEK</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td>
							<input type="text" v-model="day" required/>
						</td>
						<td>
							<input type="number" v-model="cs" required/>
						</td>
						<td>
							<input type="number" v-model="logistics" required/>
						</td>
						<td>
							<input type="number" v-model="store" required/>
						</td>
						<td>
							<input type="number" v-model="total" required/>
						</td>
						<td>
							<input type="number" v-model="prev_week" required/>
						</td>
					</tr>
				</tbody>
			</table>
			<br/>
			<div class="center">
				<button type="submit" class="btn btn-large">UPDATE</button>
			</div>
			<div class="fixed-action-btn">
				<router-link to="/cs" class="btn-floating btn-large red">
					<i class="fa fa-arrow-left"></i>
				</router-link>
			</div>
		</form>
	</div>
</template>

<script>
	import db from './firebaseInit'
	export default {
		name: 'edit-cs',
		data () {
			return {
				cs_id: null,
				day: null,
				cs: null,
				logistics: null,
				store: null,
				total: null,
				prev_week: null
			}
		},
		beforeRouteEnter (to, from, next) {
			db.collection('customerService').where('cs_id', '==', to.params.cs_id).get()
			.then(querySnapshot => {
				querySnapshot.forEach(doc => {
					next(vm => {
						vm.cs_id = doc.data().cs_id
						vm.day = doc.data().day
						vm.cs = doc.data().cs
						vm.logistics = doc.data().logistics
						vm.store = doc.data().store
						vm.total = doc.data().total
						vm.prev_week = doc.data().prev_week
					})
				})
			})
		},
		watch: {
			'$route': 'fetchData'
		},
		methods: {
			fetchData () {
				db.collection('customerService').where
				('cs_id', '==', this.$route.params.cs_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						this.cs_id = doc.data().cs_id
						this.day = doc.data().day
						this.cs = doc.data().cs
						this.logistics = doc.data().logistics
						this.store = doc.data().store
						this.total = doc.data().total
						this.prev_week = doc.data().prev_week
					})
				})
			},
			updateCs () {
				db.collection('customerService').where('cs_id', '==', this.$route.params.cs_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						doc.ref.update({
							cs_id: this.cs_id,
							day: this.day,
							cs: this.cs,
							logistics: this.logistics,
							store: this.store,
							total: this.total,
							prev_week: this.prev_week
						})
						.then(() => {
							this.$router.push({name: 'view-cs', params: {cs_id: this.cs_id}})
						})
					})
				})
			}
		}
	}
</script>