<template>
	<div id="cs">
		<table class="striped">
			<thead class="orange darken-4 white-text">
				<tr>
					<th>DAYS</th>
					<th>CUSTOMER SERVICE</th>
					<th>LOGISTICS</th>
					<th>STORE</th>
					<th>TOTAL</th>
					<th>PREV WEEK</th>
					<th></th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="customer in customerService" v-bind:key="customerService.id">
					<td>{{customer.day}}</td>
					<td>{{customer.cs}}</td>
					<td>{{customer.logistics}}</td>
					<td>{{customer.store}}</td>
					<td>{{customer.total}}</td>
					<td>{{customer.prev_week}}</td>
					<td>
						<router-link v-bind:to="{name: 'view-cs', params: {cs_id: customer.cs_id}}" class="secondary-content"><i class="fa fa-eye"></i></router-link>
					</td>
				</tr>
			</tbody>
		</table>
<!-- 		<div class="fixed-action-btn">
			<router-link to="/new-cs" class="btn-floating btn-large red">
				<i class="fa fa-plus"></i>
			</router-link>
		</div> -->
	</div>
</template>

<script>
	import db from './firebaseInit'
	export default {
		name: 'cs',
		data () {
			return {
				customerService: []
			}
		},
		created () {
			db.collection('customerService').orderBy('cs_id').get().then
			(querySnapshot => {
				querySnapshot.forEach(doc => {
					// console.log(doc.data());
					const data = {
						'id': doc.id,
						'cs_id': doc.data().cs_id,
						'day': doc.data().day,
						'cs': doc.data().cs,
						'logistics': doc.data().logistics,
						'store': doc.data().store,
						'total': doc.data().total,
						'prev_week': doc.data().prev_week
					}
					this.customerService.push(data)
				})
			})
		}
	}
</script>