<template>
	<div id="view-cs">
		<table>
			<thead class="orange darken-4 white-text">
				<tr>
					<th>DAYS</th>
					<th>CUSTOMER SERVICE</th>
					<th>LOGISTICS</th>
					<th>STORE</th>
					<th>TOTAL</th>
					<th>PREV WEEK</th>
				</tr>
			</thead>
			<tbody>
				<tr>
					<td>{{day}}</td>
					<td>{{cs}}</td>
					<td>{{logistics}}</td>
					<td>{{store}}</td>
					<td>{{total}}</td>
					<td>{{prev_week}}</td>
				</tr>
			</tbody>
		</table>
		<br/>
		<div class="center">
			<router-link v-bind:to="{name: 'edit-cs', params: {cs_id: cs_id}}"class="btn btn-large">UPDATE</router-link>
			<!-- <button @click="deleteLogistic" class="btn btn-large red">DELETE</button> -->
		</div>
		<div class="fixed-action-btn">
			<router-link to="/cs" class="btn-floating btn-large red">
				<i class="fa fa-arrow-left"></i>
			</router-link>
		</div>
	</div>
</template>

<script>
	import db from './firebaseInit'
	export default {
		name: 'view-cs',
		data () {
			return {
				cs_id: null,
				day: null,
				cs: null,
				logistics: null,
				store: null,
				total: null,
				prev_week: null
			}
		},
		beforeRouteEnter (to, from, next) {
			db.collection('customerService').where('cs_id', '==', to.params.cs_id).get()
			.then(querySnapshot => {
				querySnapshot.forEach(doc => {
					next(vm => {
						vm.cs_id = doc.data().cs_id
						vm.day = doc.data().day
						vm.cs = doc.data().cs
						vm.logistics = doc.data().logistics
						vm.store = doc.data().store
						vm.total = doc.data().total
						vm.prev_week = doc.data().prev_week
					})
				})
			})
		},
		watch: {
			'$route': 'fetchData'
		},
		methods: {
			fetchData () {
				db.collection('logistics').where
				('cs_id', '==', this.$route.params.cs_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						this.cs_id = doc.data().cs_id
						this.day = doc.data().day
						this.cs = doc.data().cs
						this.logistics = doc.data().logistics
						this.store = doc.data().store
						this.total = doc.data().total
						this.prev_week = doc.data().prev_week
					})
				})
			}
		}
	}
</script>