<template>
	<div id="edit-logistics">
		<form @submit.prevent="updateLogistics"class="">
			<table>
				<thead class="orange darken-4 white-text">
					<tr>
						<th>Month</th>
						<th>Jan</th>
						<th>Feb</th>
						<th>Mar</th>
						<th>Apr</th>
						<th>May</th>
						<th>Jun</th>
						<th>Jul</th>
						<th>Aug</th>
						<th>Sep</th>
						<th>Oct</th>
						<th>Nov</th>
						<th>Dec</th>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td>
							<!-- <input type="number" v-model="logistics_id" required disabled/> -->
							<input type="text" v-model="month" required/>
						</td>
						<td>
							<input type="number" v-model="january" required/>
							<input type="number" v-model="january_percent" required/>
						</td>
						<td>
							<input type="number" v-model="february" required/>
							<input type="number" v-model="february_percent" required/>
						</td>
						<td>
							<input type="number" v-model="march" required/>
							<input type="number" v-model="march_percent" required/>
						</td>
						<td>
							<input type="number" v-model="april" required/>
							<input type="number" v-model="april_percent" required/>
						</td>
						<td>
							<input type="number" v-model="may" required/>
							<input type="number" v-model="may_percent" required/>
						</td>
						<td>
							<input type="number" v-model="june" required/>
							<input type="number" v-model="june_percent" required/>
						</td>
						<td>
							<input type="number" v-model="july" required/>
							<input type="number" v-model="july_percent" required/>
						</td>
						<td>
							<input type="number" v-model="august" required/>
							<input type="number" v-model="august_percent" required/>
						</td>
						<td>
							<input type="number" v-model="september" required/>
							<input type="number" v-model="september_percent" required/>
						</td>
						<td>
							<input type="number" v-model="october" required/>
							<input type="number" v-model="october_percent" required/>
						</td>
						<td>
							<input type="number" v-model="november" required/>
							<input type="number" v-model="november_percent" required/>
						</td>
						<td>
							<input type="number" v-model="december" required/>
							<input type="number" v-model="december_percent" required/>
						</td>
					</tr>
				</tbody>
			</table>
			<br/>
			<div class="center">
				<button type="submit" class="btn btn-large">UPDATE</button>
			</div>
			<div class="fixed-action-btn">
				<router-link to="/" class="btn-floating btn-large red">
					<i class="fa fa-arrow-left"></i>
				</router-link>
			</div>
		</form>
	</div>
</template>

<script>
	import db from './firebaseInit'
	export default {
		name: 'edit-logistics',
		data () {
			return {
				logistics_id: null,
				month: null,
				january: null,
				february: null,
				march: null,
				april: null,
				may: null,
				june: null,
				july: null,
				august: null,
				september: null,
				october: null,
				november: null,
				december: null,
				january_percent: null,
				february_percent: null,
				march_percent: null,
				april_percent: null,
				may_percent: null,
				june_percent: null,
				july_percent: null,
				august_percent: null,
				september_percent: null,
				october_percent: null,
				november_percent: null,
				december_percent: null
			}
		},
		beforeRouteEnter (to, from, next) {
			db.collection('logistics').where('logistics_id', '==', to.params.logistics_id).get()
			.then(querySnapshot => {
				querySnapshot.forEach(doc => {
					next(vm => {
						vm.logistics_id = doc.data().logistics_id
						vm.month = doc.data().month
						vm.january = doc.data().january
						vm.february = doc.data().february
						vm.march = doc.data().march
						vm.april = doc.data().april
						vm.may = doc.data().may
						vm.june = doc.data().june
						vm.july = doc.data().july
						vm.august = doc.data().august
						vm.september = doc.data().september
						vm.october = doc.data().october
						vm.november = doc.data().november
						vm.december = doc.data().december
						vm.january_percent = doc.data().january_percent 
						vm.february_percent = doc.data().february_percent 
						vm.march_percent = doc.data().march_percent 
						vm.april_percent = doc.data().april_percent 
						vm.may_percent = doc.data().may_percent 
						vm.june_percent = doc.data().june_percent 
						vm.july_percent = doc.data().july_percent 
						vm.august_percent = doc.data().august_percent 
						vm.september_percent = doc.data().september_percent 
						vm.october_percent = doc.data().october_percent 
						vm.november_percent = doc.data().november_percent 
						vm.december_percent = doc.data().december_percent 
					})
				})
			})
		},
		watch: {
			'$route': 'fetchData'
		},
		methods: {
			fetchData () {
				db.collection('logistics').where
				('logistics_id', '==', this.$route.params.logistics_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						this.logistics_id = doc.data().logistics_id
						this.month = doc.data().month
						this.january = doc.data().january
						this.february = doc.data().february
						this.march = doc.data().march
						this.april = doc.data().april
						this.may = doc.data().may
						this.june = doc.data().june
						this.july = doc.data().july
						this.august = doc.data().august
						this.september = doc.data().september
						this.october = doc.data().october
						this.november = doc.data().november
						this.december = doc.data().december
						this.january_percent = doc.data().january_percent 
						this.february_percent = doc.data().february_percent 
						this.march_percent = doc.data().march_percent 
						this.april_percent = doc.data().april_percent 
						this.may_percent = doc.data().may_percent 
						this.june_percent = doc.data().june_percent 
						this.july_percent = doc.data().july_percent 
						this.august_percent = doc.data().august_percent 
						this.september_percent = doc.data().september_percent 
						this.october_percent = doc.data().october_percent 
						this.november_percent = doc.data().november_percent 
						this.december_percent = doc.data().december_percent 
					})
				})
			},
			updateLogistics () {
				db.collection('logistics').where('logistics_id', '==', this.$route.params.logistics_id).get()
				.then(querySnapshot => {
					querySnapshot.forEach(doc => {
						doc.ref.update({
							logistics_id: this.logistics_id,
							month: this.month,
							january: this.january,
							february: this.february,
							march: this.march,
							april: this.april,
							may: this.may,
							june: this.june,
							july: this.july,
							august: this.august,
							september: this.september,
							october: this.october,
							november: this.november,
							december: this.december,
							january_percent: this.january_percent,
							february_percent: this.february_percent,
							march_percent: this.march_percent,
							april_percent: this.april_percent,
							may_percent: this.may_percent,
							june_percent: this.june_percent,
							july_percent: this.july_percent,
							august_percent: this.august_percent, 
							september_percent: this.september_percent,
							october_percent: this.october_percent,
							november_percent: this.november_percent,
							december_percent: this.december_percent 
						})
						.then(() => {
							this.$router.push({name: 'view-logistics', params: {logistics_id: this.logistics_id}})
						})
					})
				})
			}
		}
	}
</script>